<template>
  <div>
    <grid-toolbar toolbarTitle="Slave Devices">
      <template v-slot:gridCounter>
        <grid-items-counter :num="selectedItems.length"></grid-items-counter>
      </template>
      <template v-slot:search>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          class="pl-10"
        ></v-text-field>
      </template>
      <template v-slot:customAction>
        <v-btn
          @click="() => removeSlaves(selectedItems)"
          :disabled="selectedItems.length == 0"
          x-small
          color="pink"
          class="white--text"
        >
          Remove as slave
          <v-icon x-small right dark> mdi-delete </v-icon>
        </v-btn>
      </template>
    </grid-toolbar>
    <v-data-table
      v-model="selectedItems"
      item-key="global_uuid"
      show-select
      :height="tableHeight"
      :loading="isFetching"
      :search="search"
      fixed-header
      :headers="headers"
      :items="dataItems"
      sort-by="calories"
      class="elevation-1"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:item.client_name="{ item }" class="pa-0">
        <span>
          <master-star-indicator :is-master="item.is_master"></master-star-indicator>
          <span>{{ item.client_name }}</span>
        </span>
      </template>
      <template v-slot:item.is_online="{ item }">
        <div v-if="item.is_online">
          <span class="font-weight-medium">
            <v-chip outlined color="green">Online</v-chip>
          </span>
        </div>
        <div v-else>
          <span class="font-weight-medium">
            <v-chip outlined color="yellow">Offline</v-chip>
          </span>
        </div>
      </template>
      <template v-slot:item.faliureRate="{ item }">
        <span v-if="item.faliureRate < 10" class="light-green--text font-weight-bold">{{ item.faliureRate }}</span>
        <span v-if="item.faliureRate >= 10" class="orange--text font-weight-bold">{{ item.faliureRate }}</span>
      </template>
      <template v-slot:item.actions="{ item }">
        <div>
          <tooltip-icon-button
            btnColor="blue"
            v-if="appSlaveId !== item.global_uuid || !appSlaveId"
            buttonSize="medium"
            btnIcon="mdi-toggle-switch-off-outline"
            btnTooltip="Switch to slave mode"
            :onClick="() => switchToSlave(item)"
          ></tooltip-icon-button>
          <tooltip-icon-button
            v-if="appSlaveId === item.global_uuid && appSlaveId"
            btnColor="orange"
            buttonSize="medium"
            btnIcon="mdi-toggle-switch-outline"
            btnTooltip="Revert to master mode"
            :onClick="() => removeAppSlaveId()"
          ></tooltip-icon-button>
          <tooltip-icon-button
            btnColor="red"
            btnIcon="mdi-delete"
            btnTooltip="Remove as slave"
            :onClick="() => removeSlaves(item)"
            :loading="item.isDeleting"
          ></tooltip-icon-button>
          <!-- <v-btn
            v-if="appSlaveId !== item.global_uuid || !appSlaveId"
            @click="() => switchToSlave(item)"
            small
            color="primary"
            class="white--text"
          >
            <v-icon right dark> mdi-toggle-switch-off-outline </v-icon>
          </v-btn>
          <v-btn
            v-if="appSlaveId === item.global_uuid && appSlaveId"
            @click="removeAppSlaveId"
            small
            color="orange"
            class="white--text"
          >
            <v-icon right dark> mdi-toggle-switch-outline </v-icon>
          </v-btn>
          <v-btn small color="pink" @click="() => removeSlaves(item)" :loading="item.isDeleting">
            <v-icon mdi-small right dark> mdi-delete </v-icon>
          </v-btn> -->
        </div>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import GridToolbar from '../Common/GridToolbar';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import MasterStarIndicator from '../Common/MasterStarIndicator.vue';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import GridItemsCounter from '../Common/gridItemsCounter.vue';

const MAX_VISIBLE_GRID_ITEMS = 20;

export default {
  name: 'slave-devices',
  components: { GridToolbar, MasterStarIndicator, TooltipIconButton, GridItemsCounter },
  props: ['items', 'removeSlaves', 'isFetching', 'switchToSlave', 'removeAppSlaveId', 'appSlaveId'],
  data: () => ({
    search: '',
    selectedItems: [],
    headers: [
      { text: 'S/N', align: 'center', sortable: true, value: 'sn' },
      {
        text: 'Client Name',
        align: 'start',
        sortable: true,
        value: 'client_name',
      },
      { text: 'Status', align: 'center', value: 'is_online', sortable: true },
      { text: 'Device Name', align: 'center', value: 'device_name', sortable: true },
      { text: 'Site Name', align: 'center', value: 'site_name', sortable: true },
      { text: 'Latitude', align: 'center', value: 'site_lat', sortable: true },
      { text: 'Longitude', align: 'center', value: 'site_lon', sortable: true },
      { text: 'Site Address', align: 'center', value: 'site_address', sortable: true },
      { text: 'Failiure Rate (%)', align: 'center', value: 'faliureRate', sortable: true },
      { text: 'Actions', align: 'center', value: 'actions', sortable: true },
    ],
    tableProps: { ...DATA_TABLE_PROPS },
  }),
  computed: {
    dataItems() {
      return this.items.map((item, index) => ({
        ...item,
        sn: index + 1,
        faliureRate: ((item.failure_count / item.total_count) * 100).toFixed(2),
        client_name: item.is_master ? `   ${item.client_name}` : item.client_name,
      }));
    },
    tableHeight() {
      return this.items && this.items.length > MAX_VISIBLE_GRID_ITEMS ? '75vh' : undefined;
    },
  },
};
</script>
<style>
.v-data-table__wrapper::-webkit-scrollbar {
  width: 10px;
}

.v-data-table__wrapper::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.v-data-table__wrapper::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.v-data-table__wrapper::-webkit-scrollbar-thumb:hover {
  background: white;
}
</style>
